<template>
  <div class="my-address">
  
      <van-nav-bar title="地址管理" left-text="返回" left-arrow :fixed="true" @click-left="returnLast" />
       <div v-if="list.length > 0">
       </div>

     <div v-else>
           <van-empty description="地址空空如也，去添加一个吧!">
            </van-empty>
         </div>
    <div class="address">
      <van-address-list v-model="chosenAddressId" :list="list" default-tag-text="默认" @add="newAddress" @edit="editAddress" />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //选择地址ID
      chosenAddressId: "",
      //地址列表数据
      list: []
    };
  },
  created() {
    //查询收货地址
    this.getReceiveAddress();
  },
  methods: {
    //返回上一级
    returnLast() {
      this.$router.go(-1);
    },

    //查询收货地址
    getReceiveAddress() {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push({name:'Login'})
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner"
      });

      this.axios({
        method: "GET",
        url: "/findAddress",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 20000) {
            result.data.result.map(v => {
              let address = v.province + v.county + v.addressDetail;
              this.list.push({
                id: v.aid,
                name: v.name,
                tel: v.tel,
                address,
                isDefault: Boolean(v.isDefault)
              });
            });
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //新增地址
    newAddress() {
      this.$router.push({ name: "NewAddress" });
    },

    //编辑地址
    editAddress(item) {
        
        this.$router.push({name:'NewAddress',query:{aid:item.id}});
    }
  }
};
</script>

<style lang="less" scoped>
/deep/.van-nav-bar .van-icon {
  color: #0c34ba;
}
/deep/.van-nav-bar__text {
  color: #0c34ba;
}
/deep/.van-switch--on {
  background-color: #0c34ba;
}
/deep/.van-button--danger {
  background-color: #0c34ba;
  border-color: #0c34ba;
}
/deep/.van-tag--danger {
  background-color: #0c34ba;
}
/deep/.van-radio__icon {
  display: none;
}
.address {
  margin-top: 46px;
}
</style>